.Taskbar{
    height: 4vh;
    width: 98.5vw;
    min-height: 2rem;
    padding: 0 0.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 2px solid rgb(63, 66, 66);
    background-color: rgb(43, 46, 46);
    position: fixed;
    z-index: 20;
}

@media(max-width : 850px){
    .Taskbar{
        width: 98.5vw;
    }
}

.Items{
    height: 100%;
    width: 30vw;
    display: flex;
}

.Center{
    color: white;
    width: 30vw;
    min-width: 275px;
    padding: 0.3rem 0;
    box-sizing: border-box;
    font-size:smaller;
    display: flex;
    align-items: center;
    flex-flow: row;
}
.CenterLogo{
    display: none;
}

@media(max-width:850px){
    .Taskbar{
        justify-content: space-between;
    }
    .Items{
        width: 10vw;
        display: none;
    }
    .Center{
        width: 50vw;
    }
    .CenterLogo{
        display: flex;
    }
}



.button{
    display: flex;
    flex-flow: row;
}